    .page {
        text-align: left;
        padding: 30px;
    }

    .header {
        .input {
            min-width: 150px;
            max-width: 70%;
        }
        .header-col {
            height: 60px;
        }
        .header-col-select {
            width: 80%;
        }
    }

    .input {
        min-width: 150px;
        max-width: 70%;
