    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

    .num-input {
        width: 100px;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">
                <el-col class="filter-label" :span="2">规则编号:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.id" placeholder="请输入"></el-input>
                </el-col>

                <el-col class="filter-label" :span="2"> 状态:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="filter.recommend" placeholder="请选择">
                        <el-option
                                v-for="item in statusOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col class="filter-label" :span="2"> 回顾标题:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.title" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
                </el-col>


            </el-row>
            <el-row class="filter">
                <el-col class="filter-label" :span="2"> 发布日期:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
            </el-row>
        </div>

        <el-table
                :data="data.data"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label="ID">
            </el-table-column>
            <el-table-column
                    prop="title"
                    label="标题">
            </el-table-column>
            <el-table-column
                    prop="publish_time"
                    label="发布时间">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="recommend"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.recommend===1?'推荐':'不推荐'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="detail(scope.row)" type="text" size="small">详细</el-button>
                    <el-button type="text" @click="del(scope)" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="data.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import api from '../../js/api/review'

    export default {
        data() {
            return {
                dialog: {
                    dialogVisible: false,
                    content: "",
                },

                statusOptions: [{label: '推荐', value: 1}, {label: '不推荐', value: 0}],

                formSource: [
                    {
                        value: '选项1',
                        label: '百度'
                    },
                    {
                        value: '选项2',
                        label: 'Google'
                    }
                ],
                filter: {
                    recommend: null,
                    id: null,
                    time: null,
                    title: null,
                    start_time: null,
                    end_time: null,
                    jump_page: null,
                },
                data: {}
            }
        },
        mounted() {
            this.loadData(1)
        },
        methods: {

            ref() {
                this.filter = {
                    recommend: null,
                    id: null,
                    time: null,
                    title: null,
                    starttime: null,
                    endtime: null,
                    jump_page: null,
                };
                this.loadData(1)
            },

            loadData(page) {
                this.filter.jump_page = page;
                if (this.filter.time != null && this.filter.time.length === 2) {
                    this.filter.start_time = this.filter.time[0];
                    this.filter.end_time = this.filter.time[1]
                } else {
                    this.filter.start_time = null;
                    this.filter.end_time = null;
                }
                api.index(this.filter, res => {
                    this.data = res.data.data;
                })

            },
            filterOpen() {
                this.filter.isOpen = !this.filter.isOpen;
            },
            add() {
                this.$router.push({
                    name: "salonReviewAdd",
                });
            },
            detail(item) {
                this.$router.push({
                    name: "salonReviewAdd",
                    query: {
                        id: item.id,
                    },
                });
            },
            del(scope) {
                let _this = this;
                this.$confirm('此操作将永久删除' + scope.row.title + ', 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);

                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
        }
    }
</script>

